
import './App.css';
import Navbar from './components/Navbar';
import Form from './components/Form';
// import About from './components/About';
import React, { useState } from 'react';

import Alert from './components/Alert';

// import {
//   BrowserRouter as Router,
//   Switch,
//   Route
// } from "react-router-dom";
function App() {
  const [Mode, setMode] = useState('light');// whether dark mode is enable or not
  const [alert, setAlert] = useState(null);

  const showAlert = (message, type) => {
    setAlert({
      msg: message,
      type: type
    })
    setTimeout(() => {
      setAlert(null)
    }, 1500);
  }
  const toggelMode = () => {
    if (Mode === 'light') {
      setMode('dark');
      document.body.style.backgroundColor = '#0d1536';//#212529
      showAlert("Dark mode is enabled", "success");
      document.title = 'Txt.chngr-Dark Mode';
    }
    else {
      setMode('light');
      document.body.style.backgroundColor = 'white';//#fff8e8
      showAlert("Light mode is enabled", "success");
      document.title = 'Txt.chngr-Light Mode';
    }
  }
  return (
    <>
    {/* <Router> */}
      <Navbar usingprops="Txt.chngr" mode={Mode} toggelMode={toggelMode} />
      <Alert Alert={alert} />
      <div className="container my-3">
        {/* <Switch> */}
          {/* <Route exact path="/about"> */}
            {/* <About /> */}
          {/* </Route> */}
          {/* <Route exact path="/"> */}
            <Form showAlert={showAlert} heading="Enter your text for analysing" mode={Mode} />
          {/* </Route> */}
        {/* </Switch> */}
      </div>
      {/*  </Router> */}
    </>
  );
}


export default App;
